#app{
  min-height: 100vh;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  min-width: 100%;
}
.swiper {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  height: 100% !important;
  text-align: center;
  font-size: 18px;
  background: #fff;

  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height:100% !important;
  object-fit: cover;
}

.divRoutes{
  margin-top: 3rem;
}
